import GameBanner from "./GameBanner";

const GameOver = (props) => {
    return(
        <div className="gameover major-div">
            <GameBanner />
            <div className="game-over-statements">
                <div>Kudos!</div>
                <div>
                    <div>Our total sales is:</div>
                    <div>P {props.sales}</div>
                </div>
                <div>
                    <button onClick={props.playAgain}>Let's do this again!</button>
                </div>                    
            </div>
        </div>
    )
}

export default GameOver;