import GameBanner from "./GameBanner";

const Instructions = (props) => {
    return(
        <div className="instructions major-div">
            <GameBanner />
            <p>Howdy?</p>
            <p>I've got a lot customers today lining up for my fresh fruits. Please help me out by clicking the fruits that they order from my fruits box.</p>
            <div>
                <button onClick={props.startGame}>Play!</button>
            </div>
        </div>
    )
}

export default Instructions;