import { Component } from "react";

class FruitBtn extends Component{
    render(){
        return(
            <button
                onClick={()=>this.props.checkChoice(this.props.fruit.name)}
            >
                <img
                    src = {this.props.fruit.img}
                    alt = {this.props.fruit.name}
                />
            </button>
        )
    }
}

export default FruitBtn;