import "./assets/FruitsStore.css"
import { useState } from "react";

import Instructions from "./Instructions";
import GameDiv from "./GameDiv";
import GameOver from "./GameOver";

import Apple from "./assets/imgs/apple.png"
import Banana from "./assets/imgs/banana.png"
import Orange from "./assets/imgs/orange.png"
import Grapes from "./assets/imgs/grapes.png"
import Avocado from "./assets/imgs/avocado.png"
import Kiwi from "./assets/imgs/kiwi.png"
import Lemon from "./assets/imgs/lemon.png"
import Mango from "./assets/imgs/mango.png"
import Pineapple from "./assets/imgs/pineapple.png"
import Strawberry from "./assets/imgs/strawberry.png"
import Tomato from "./assets/imgs/tomato.png"
import Watermelon from "./assets/imgs/watermelon.png"
import Dragonfruit from "./assets/imgs/dragonfruit.png"
import Melon from "./assets/imgs/melon.png"
import Blueberries from "./assets/imgs/blueberries.png"


const TootieFruity = (props) => {
    let [instDisplayStat, setInstDisplayStat] = useState(true);
    let [gameDisplayStat, setGameDisplayStat] = useState(false);
    let [gameOverDisplayStat, setGameOverDisplayStat] = useState(false);
    let [sales, setSales] = useState(0);
    let [timer, setTimer] = useState(30);
    let [randomOrder, setRandomOrder] = useState(0);
    let fruits = [
        {
            name: "Apple",
            img: Apple,
            price: 20
        },
        {
            name: "Banana",
            img: Banana,
            price: 15
        },    
        {
            name: "Orange",
            img: Orange,
            price: 25
        },    
        {
            name: "Grapes",
            img: Grapes,
            price: 35
        }, 
        {
            name: "Avocado",
            img: Avocado,
            price: 50
        }, 
        {
            name: "Kiwi",
            img: Kiwi,
            price: 25
        }, 
        {
            name: "Lemon",
            img: Lemon,
            price: 15
        }, 
        {
            name: "Mango",
            img: Mango,
            price: 25
        }, 
        {
            name: "Pineapple",
            img: Pineapple,
            price: 70
        }, 
        {
            name: "Strawberry",
            img: Strawberry,
            price: 50
        }, 
        {
            name: "Tomato",
            img: Tomato,
            price: 10
        }, 
        {
            name: "Watermelon",
            img: Watermelon,
            price: 100
        }, 
        {
            name: "Dragonfruit",
            img: Dragonfruit,
            price: 80
        }, 
        {
            name: "Melon",
            img: Melon,
            price: 90
        }, 
        {
            name: "Blueberries",
            img: Blueberries,
            price: 120
        }, 
    ]
    const startGame = () => {
        setInstDisplayStat(false);
        setGameDisplayStat(true);
        generateOrder();
        startTimer();
    }
    const startTimer = () => {
        let id = setInterval( () => {
            let seconds;
            setTimer(timer => seconds = timer - 1);
            if(seconds === 0){
                clearInterval(id);
                setGameDisplayStat(false);
                setGameOverDisplayStat(true);
            }
        },1000);
    }
    const generateOrder = () => {
        setRandomOrder(Math.floor(Math.random()*fruits.length))
    }
    const addToSales = (amt) => {
        setSales(sales + amt);
    }
    const playAgain = () => {
        setSales(0);
        setGameOverDisplayStat(false);
        setGameDisplayStat(true);
        setTimer(30);
        generateOrder()
        startTimer();
    }
    return(
        <div className="main-box">
            {
                instDisplayStat &&
                <Instructions
                    startGame = {startGame}
                />
            }
            {
                gameDisplayStat &&
                <GameDiv
                    fruits = {fruits}
                    randomOrder = {randomOrder}
                    sales = {sales}
                    timer = {timer}
                    generateOrder = {generateOrder}
                    addToSales = {addToSales}
                />
            }
            {
                gameOverDisplayStat &&
                <GameOver 
                    sales = {sales}
                    playAgain = {playAgain}
                />
            }
        </div>
    )
}

export default TootieFruity;