import FruitBtns from "./FruitBtns";
import FruitOrder from "./FruitOrder";
import GameBanner from "./GameBanner";
import ScoreBox from "./ScoreBox";
import TimerBox from "./TimerBox";

const FruitsStore = (props) => {
    const checkChoice = (playerChoice) => {
        if (playerChoice === props.fruits[props.randomOrder].name) {
            props.addToSales(
                props.fruits[props.randomOrder].price
            )
            props.generateOrder();
        }
    }
    return(
        <div className="game-div major-div">
            <GameBanner/>
            <div>
                <ScoreBox
                    sales = {props.sales}
                />
                <TimerBox
                    timer = {props.timer}
                />
            </div>
            <div id="fruits-display">
                <FruitOrder
                    randomOrder = {props.randomOrder}
                    fruits = {props.fruits}
                />
                <FruitBtns
                    fruits = {props.fruits}
                    checkChoice = {checkChoice}
                />
            </div>
        </div>
    );
};

export default FruitsStore;