import FruitBtn from "./FruitBtn";

const FruitBtns = (props) => {
    return(
        <div className="fruit-btns">
            {
                props.fruits.map((fruit)=>
                    <FruitBtn
                        key = {fruit.name}
                        fruit = {fruit}
                        checkChoice = {props.checkChoice}
                    />
                )
            }
        </div>
    )
}

export default FruitBtns;